import axios from 'axios'
import qs from 'qs';

axios.defaults.timeout = 60000
axios.defaults.baseURL = '/api';
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

axios.interceptors.request.use(function (config) {
  return config;
}, function (error) {
  return Promise.reject(error);
});

axios.interceptors.response.use(function (response) {
  return response;
}, function (error) {
  return Promise.reject(error);
});

export default {
  getUser: params => {
    return axios.get('/user/get?name=yangjing', qs.stringify(params)).then(res => res.data);
  },
  postUser: params => {
    return axios.post('/user/post', params).then(res => res.data);
  },
  getArticle: params => {
    return axios.get(`/article/${params}`).then(res => res.data);
  },
  articleLove: params => {
    return axios.get(`/article/love/${params}`).then(res => res.data);
  },
  getArticleList: params => {
    return axios.get('/article', params).then(res => res.data);
  },
  getArticleTags: params => {
    return axios.get('/tag', qs.stringify(params)).then(res => res.data);
  },
  getCategory: params => {
    return axios.get('/category', qs.stringify(params)).then(res => res.data);
  },
  saveArticle: params => {
    return axios.post('/article', params).then(res => res.data);
  },
  getFriendLink: params => {
    return axios.get('/friendLink', qs.stringify(params)).then(res => res.data);
  },
}