<template>
  <div>
    <a-form :form="form" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" @submit="handleSubmit">
      <a-form-item label="title">
        <a-input
          v-decorator="['title', { rules: [{ required: true, message: 'Please input article title!' }] }]"
        />
      </a-form-item>
      <a-form-item label="category">
        <a-select
          v-decorator="['categoryId', { rules: [{ required: true, message: 'Please select category!' }] }]"
        >
          <a-select-option v-for="e in category" :key="e.id">
            {{e.name}}
          </a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="summary">
        <a-textarea
          v-decorator="['summary', { rules: [{ required: true, message: 'Please input article summary!' }] }]"
        />
      </a-form-item>
      <a-form-item label="tags">
        <a-select
          v-decorator="['tags', { rules: [{ required: true, message: 'Please select article tags!' }] }]"
          mode="multiple"
        >
          <a-select-option v-for="tag in tags" :key="tag.id">
            {{tag.name}}
          </a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="isTop">
        <a-switch
          v-decorator="['isTop']"
        />
      </a-form-item>
      
      <div :class="prefixCls">
        <quill-editor
          v-model="content"
          ref="myQuillEditor"
          :options="editorOption"
          >
        </quill-editor>

      </div>
      <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
        <a-button type="primary" html-type="submit">
          Submit
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
// import { Form } from 'ant-design-vue';import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import axios from '@/api';

export default {
  data() {
    return {
      formLayout: 'horizontal',
      form: this.$form.createForm(this, { name: 'coordinated' }),
      tags: [],
      category: [],
      content: null,
      editorOption: {
        // some quill options
      }
    }
  },
  components: {
    quillEditor,
  },
  props: {
    prefixCls: {
      type: String,
      default: 'ant-editor-quill'
    },
    // 表单校验用字段
    // eslint-disable-next-line
    value: {
      type: String
    }
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          // alert(JSON.stringify(values))
          // console.log('Received values of form: ', values);
          
          axios.saveArticle({ ...values, content: this.content, tags: values.tags.map(item => ({id: item}))}).then(res => {
            if (res === 1) {
              this.$router.push('/article')
            }
          })
        }
      });
    },

    // onEditorBlur (quill) {
    //   // console.log('editor blur!', quill)
    // },
    // onEditorFocus (quill) {
    //   // console.log('editor focus!', quill)
    // },
    // onEditorReady (quill) {
    //   // console.log('editor ready!', quill)
    // },
    // onEditorChange ({ quill, html, text }) {
    //   // console.log('editor change!', quill, html, text)
    //   this.$emit('change', html)
    // }
  },
  watch: {
    value (val) {
      this.content = val
    }
  },
  mounted() {
    axios.getArticleTags().then(res => {
      // this.articleList = res.data.articleList;
      this.tags = res;
    });
    axios.getCategory().then(res => {
      this.category = res;
    })
  }
}
</script>

